import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";

const MasterPasswordEnter = ({step, setStep, MASTER}) => {

    const navigate = useNavigate();

    const [password, setPassword] = useState(MASTER.password);

    const click = async () =>{
        try {
            MASTER.password = password;
            await MASTER.logInMaster();
            navigate('/');
        } catch (error) {
            if(error.message == ' ' || error.message ==  '  8 ' ||
            error.message == '   1 ' || error.message ==  '   1 ' ||
            error.message == '  '
            ){
                console.log(error.message);
            }else{
                throw new Error(error.message);
            }
        }
    }

    return(<div className="phases-wrapper">
        <input type="password" placeholder="" value={password} onChange={e => setPassword(e.target.value)} className="text-input-field"></input>
        <button onClick={click} className='continue-button'></button>
    </div>);
};

export default MasterPasswordEnter;