import { useEffect, useState } from 'react';
import InputWithError from '../../../../animations/input-error-field';

const MasterOccupationEnter = ({step, setStep, MASTER}) => {
    const [error, setError] = useState(false);
    const [warning, setWarning] = useState(false);

    const [occupation, setOccupation] = useState(MASTER.occupation);

    const activateHint = (value) => {
        setOccupation(value);
    }

    const click = async () =>{
        try {
            MASTER.occupation = occupation;
            setStep(step + 1);
        } catch (error) {
            if(error.message == ' '){
                setWarning(true);
            }
        }
    }

    return(<div className="phases-wrapper">
        <div className="signUpMaster-annotation">
            <h1>  ?</h1>
            <a>         .</a>
        </div>
        <div className="signUpMaster-inputs">
            <InputWithError placeholder={' '} value={occupation} setValue={setOccupation} 
                error={error} setError={setError} errorText={''}
                warning={warning} setWarning={setWarning} warningText={' '}></InputWithError>
            <div className="button-hints">
                <button className="hint" onClick={() => activateHint(" ")}> </button>
                <button className="hint" onClick={() => activateHint("  ")}>  </button>
                <button className="hint" onClick={() => activateHint("")}></button>
                <button className="hint" onClick={() => activateHint(" ")}> </button>
                <button className="hint" onClick={() => activateHint("")}></button>
                <button className="hint" onClick={() => activateHint(" ")}> </button>
            </div>
        </div>
        <button onClick={click} className='continue-button'></button>
    </div>);
};

export default MasterOccupationEnter;