import { useState, useEffect } from "react";

const MasterFIOEnter = ({step, setStep, MASTER}) => {

    const [fio, setFio] = useState(MASTER.fio)

    const [secondName, setSecondName] = useState(true);

    const [error, setError] = useState(false);
    const [warning, setWarning] = useState(false);

    const setNoSecondName = (value) => {
        setSecondName(!secondName);
    }

    const setFioByIndex = (value, index) => {
        const currentFio = fio.map((v, i) => {
            if(i == index){
                return v = value;
            }else{
                return v;
            }
        });

        setFio(currentFio);
    }

    const click = async () =>{
        try {
            MASTER.fio = fio;
            setStep(step + 1);
        } catch (error) {
            if(error.message == ' '){
                setWarning(true);
            }
        }
    }
    
    return(<div className="phases-wrapper">
        <div className="signUpMaster-annotation">
            <h1>   ?</h1>
            <a> ,      ,    .</a>
        </div>
        <div className="signUpMaster-inputs">
            <input placeholder="" value={fio[0]} onChange={e => setFioByIndex(e.target.value, 0)} className="text-input-field"></input>
            <input placeholder="" value={fio[1]} onChange={e => setFioByIndex(e.target.value, 1)} className="text-input-field"></input>
            {secondName?
                <input placeholder="" value={fio[2]} onChange={e => setFioByIndex(e.target.value, 2)} className="text-input-field"></input>
            :
                <></>}
            <div className="options-input" onChange={e => setNoSecondName(e.target.value)}>
                <div className="option">
                    <input type="checkbox" value={secondName}></input> 
                    <p> </p>
                </div>
            </div>
        </div>
        <button onClick={click} className='continue-button'></button>
    </div>);
};

export default MasterFIOEnter;