import { useEffect, useState } from 'react';
import InputWithError from '../../../../animations/input-error-field';

const TopicEnter = ({TOPIC}) => {
    const [topic_name, setTopic_name] = useState(TOPIC.topicName);

    const [error, setError] = useState(false);
    const [warning, setWarning] = useState(false);

    useEffect(()=>{
        try {
            TOPIC.topicName = topic_name;
        } catch (error) {
            if(error.message == ' '){
                setWarning(true);
            }else if(error.message == ' '){
                setError(true);
            }
        }
        
    },[topic_name])

    const activateHint = (value) => {
        setTopic_name(value);
    };

    return(<div className="createTopic-phase">
        <div className='createTopic-phase-annotation'>
            <p>1/9</p>
            <h1>  ?</h1>
        </div>
        <div className='createTopic-phase-inputs'>
            <InputWithError placeholder={""} value={topic_name} setValue={setTopic_name} 
                error={error} setError={setError} errorText={'  '}
                warning={warning} setWarning={setWarning} warningText={' '}></InputWithError>
            <div className="button-hints">
                <button className="hint" onClick={() => activateHint("")}>
                
                </button>
                <button className="hint" onClick={() => activateHint("")}>
                
                </button>
                <button className="hint" onClick={() => activateHint("")}>
                
                </button>
                <button className="hint" onClick={() => activateHint("")}>
                
                </button>
            </div>
        </div>
    </div>)
}

export default TopicEnter;