import { useState, useEffect } from 'react';
import Loader from '../../../Loader/Loader';
import masterController from '../../../../controllers/MASTER-controller';
import userController from '../../../../controllers/USER-controller';
import InputWithError from '../../../../animations/input-error-field';

const MasterEmailEnter = ({step, setStep, MASTER}) => {

    const [showLoader, setShowLoader] = useState(false);

    const [error, setError] = useState(false);
    const [warning, setWarning] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');

    const [email, setEmail] = useState(MASTER.email);
    const [code, setCode] = useState(MASTER.code);

    const [codeSend, setCodeSend] = useState(false);

    const sendNewCode = async () => {
        try {
            MASTER.email = email;
            setShowLoader(true);
            MASTER.sendCode();
            setCodeSend(await MASTER.codeSend());
            setShowLoader(false);
        } catch (error) {
            if(error.message == ' '){
                setShowLoader(false);
                setError(true);
                setErrorMessage('   : ****@email.com');
            }else if(error.message == ' '){
                setShowLoader(false);
                setWarning(true);
            }else if(error.message == ' '){
                setShowLoader(false);
                setError(true);
                setErrorMessage('    ');
            }else{
                setShowLoader(false);
                throw new Error(error.message);
            }
        }
    }

    const click = async () =>{
        try {
            MASTER.code = code;
            setStep(step + 1);
        } catch (error) {
            if(error.message == ' '){
                setWarning(true);
            }else if(error.message == '  '){
                setError(true);
                setErrorMessage('   ');
            }{
                throw new Error(error.message);
            }
        }
    }

    return(<div className="phases-wrapper">
        <InputWithError placeholder={''} value={email} setValue={setEmail} 
            error={error} setError={setError} errorText={errorMessage}
            warning={warning} setWarning={setWarning} warningText={' '}
            inputType='email'></InputWithError>
        {codeSend?
        <InputWithError placeholder={''} value={code} setValue={setCode} 
            error={error} setError={setError} errorText={errorMessage}
            warning={warning} setWarning={setWarning} warningText={' '}
            inputType='number'></InputWithError>
        :
        <button onClick={sendNewCode} className="send-code-button"> </button>}
        <button onClick={click} className='continue-button'></button>
        <Loader showLoader={showLoader} setShowLoader={setShowLoader} text={" "}></Loader>
    </div>);
};

export default MasterEmailEnter;